@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	//  
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<desk-sm') {
			@include scroll-blocked; } } }

/* ms-drop */
.ms-drop__drop-js {
	opacity: 0;
	visibility: hidden;

	.is-open & {
		opacity: 1;
		visibility: visible; } }

/* ms-drop end */

/* user form */
.user-form {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	.form-title {
		display: block;
		@include font-alt-bold;
		color: $title-color;
		text-align: center;
		margin-bottom: $bottom-space;

		// font size
		& {
			@include font-h2; } }

	form {
		display: block; }

	.input-wrap {
		&:last-child {
			margin-bottom: 0; } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		[class*="btn-"] {
			@include media('<mob-lg') {
				display: block;
				width: 100%;
				margin-top: $bottom-space--row;

				&:first-child {
					margin-top: 0; } } } }

	.form-note {
		overflow: hidden;
		min-height: $btn-height;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--small, $line-height--small);
		letter-spacing: $letter-spacing; } }

/* user form end */

/* slider */
.swiper-container,
.swiper-pagination,
.slider-arrows {
	opacity: 0;
	pointer-events: none;
	// transition
	$prop: opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	.is-loaded &,
	&.is-loaded,
	.no-js & {
		opacity: 1;
		pointer-events: auto; } }

// slider arrows
.slider-arrows {
	height: 0;
	position: absolute;
	width: 100%;
	left: 0;
	bottom: 100%;
	margin-bottom: $bottom-space--article;
	pointer-events: none;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--article--mob;
		display: none; } }

.slider-arrows__holder {
	position: relative; }

.slider-arrow {
	position: absolute;
	top: 50%;
	@include size(nth($slider-button-size, 2), nth($slider-button-size, 1));
	margin-top: -#{round(nth($slider-button-size, 2)/2)};
	color: $base-color;
	will-change: transform;
	//background: rgba(91, 255, 176, 0.51)
	//  ,    
	//background: none
	z-index: 5;
	cursor: pointer;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	.slider-arrows & {
		left: auto;
		top: auto;
		bottom: 0;
		pointer-events: auto;
		margin: round((50 - nth($slider-button-size, 1))/2) 0;

		@include media('<desk-sm') {
			margin: round((38 - nth($slider-button-size, 1))/2) 0; }

		@include media('<mob-sm') {
			margin: round((30 - nth($slider-button-size, 1))/2) 0; } }

	svg {
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		width: $slider-arrow-size;
		height: $slider-arrow-size;
		margin: -#{round($slider-arrow-size/2)} 0 0 -#{round($slider-arrow-size/2)};
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<desk-sm') {
			width: $slider-arrow-size--mob;
			height: $slider-arrow-size--mob;
			margin: -#{round($slider-arrow-size--mob/2)} 0 0 -#{round($slider-arrow-size--mob/2)}; }


		& + svg {
			opacity: 0; } }

	path {
		fill: currentColor; } }

// prev arrow
.slider-arrow_prev {
	left: 0;

	.slider-arrows & {
		right: nth($slider-button-size, 2); } }

// next arrow
.slider-arrow_next {
	right: 0;

	.slider-arrows & {
		right: 0; } }

// hover, disabled, active
.slider-arrow {

	// hover
	&:not(.swiper-button-disabled):hover {
		svg {
			& + svg {
				opacity: 1; } } }

	// active
	&:not(.swiper-button-disabled):active {
		svg {
			transform: translate(0, 1px); } }

	// disabled
	&.swiper-button-disabled {
		pointer-events: auto;
		cursor: default;
		opacity: 0.33; } }


/* slider pagination */
$slider-pag-size: 10px 10px; // h w

.swiper-pagination {
	pointer-events: none; }

.swiper-pagination-bullet {
	display: inline-block;
	vertical-align: top;
	@include size(#{nth($slider-pag-size, 2)}, #{nth($slider-pag-size, 1)});
	border-radius: 0;
	border: 1px solid currentColor;
	color: $base-color;
	opacity: 1;
	transform: skew($skew, 0);
	margin: 4px;
	pointer-events: auto;

	background: none; }

.swiper-pagination-bullet-active {
	background-color: currentColor; }

/* slider end */
/* tape-slider */
.tape-slider {
	position: relative;
	margin-bottom: $bottom-space--article;

	background: url(../../img/preloader.svg) no-repeat center;
	background-size: 40px;

	&.is-loaded {
		background-image: none; }

	@include media('<desk-sm') {
		@include padding-horizontal(nth($page-layout-space--md, 2));
		margin-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2));
		margin-bottom: $bottom-space--article--mob; }

	&:last-child {
		margin-bottom: 0; }

	.swiper-container {
		@include media('>=desk-sm') {
			border: 1px solid $border-color;
			margin-left: -2px;
			margin-right: -1px; } }

	.swiper-slide {
		border: 0 solid $border-color;
		border-left-width: 1px;
		background-color: $bg-color-alt;

		@include media('<desk-sm') {
			border-width: 1px; } }

	.swiper-pagination {
		margin-top: $bottom-space--article;
		position: relative;
		line-height: 0;

		@include media('<desk-sm') {
			margin-top: $bottom-space--article--md; }

		@include media('<mob-lg') {
			margin-top: $bottom-space--article--mob; } } }

/* tape-slider end */

