/* news */
.main-section--news {
	overflow: hidden;

	@include media('<desk-sm') {
		padding-top: 0; } }

.news-slider {
	margin: 0 auto $bottom-space--article;
	position: relative;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--article--mob; }

	&:last-child {
		margin-bottom: 0; }

	.swiper-container {
		overflow: visible; }

	.swiper-pagination {
		margin-top: $bottom-space--article;
		position: relative;
		line-height: 0;

		@include media('<desk-sm') {
			margin-top: $bottom-space--article--md; }

		@include media('<mob-lg') {
