/* subscription form */
.subscribe {
	background-color: #282828;
	color: white;
	@include padding-vertical(4rem); }

.subs-form {
	$subs-form-size: 50px 100%; // h w
	$subs-form-border-radius: 0;

	text-align: left;

	.input-wrap {
		display: flex;
		align-items: center;
		margin: 0; }

	.label-holder {
		width: 50%;
		margin: 0 17px 0 0; }

	.label-holder, label {
		@include font-normal;
		@include font(1.8rem, round-float(22/18, 2));
		color: inherit; }

	label {
		b {
			display: block;
			@include font-normal;
			@include font(3.0rem, round-float(30/18, 2)); } }

	.form-fields {
		width: 50%;
		position: relative;

		@include media('<mob-lg') {
			width: 100%; } }

	.input-holder {
		position: relative;
		height: nth($subs-form-size, 1);
		padding-right: 165px;
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: 0.25s; }

		input {
			height: nth($subs-form-size, 1);
			font-size: 16px;
			line-height: nth($subs-form-size, 1);
			padding: 0 20px;
			border-radius: 0;
			border: none;
			margin: 0;

			&:-webkit-autofill {
				-webkit-box-shadow: 0 0 0 30px $form-bg-color inset; }

			&:-webkit-autofill {
				-webkit-text-fill-color: $text-color; } } }

	.form-success .input-holder {
		opacity: 0;
		visibility: hidden;
		$scale: 0.76;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1);
		pointer-events: none; }

	input[type='submit'], button {
		display: inline-block;
		vertical-align: top;
		height: nth($subs-form-size, 1);
		width: 165px;
		min-width: 0;
		padding: 0 10px;
		position: absolute;
		top: 0;
		right: 0;
		border: none;
		@include font-bold;
		font-size: 18px;
		line-height: nth($subs-form-size, 1);
		letter-spacing: 0.1em;
		text-align: center;
		color: white;
		cursor: pointer;
		box-shadow: none;
		background-color: $base-color;
		border-radius: 0; }

	.subs-success {
		position: absolute;
		@include position(0);
		@include size(100%);
		opacity: 0;
		pointer-events: none;
		background-color: $base-color;
		transform: scale(0.76, 0.76) translateZ(0);
		border-radius: $subs-form-border-radius;
		//text-indent: -9999px
		//overflow: hidden
		text-align: center;
		color: white;
		font-size: 16px;
		line-height: nth($subs-form-size, 1);

		transition: {
			property: opacity, visibility, transform;
			duration: 0.25s; }

		span {
			position: relative;
			display: inline-block;
			vertical-align: top;
			//position: absolute
			//+position(50%)
			//+size(24px, 16px)
			//margin: -8px 0 0 -12px

			&::before, &::after {
				@include pseudo();
				@include size(0, 2px);
				left: 0;
				top: 50%;
				background-color: currentColor;
				$prop: width;
				will-change: $prop;
				transition: {
					property: $prop;
					duration: 0.12s;
					timing-function: linear; } }

			&::before {
				margin: -3px 0 0 -29px;
				transform: rotate(45deg) translateZ(0);
				transform-origin: 0 8px;
				transition-delay: 0.12s; }

			&::after {
				transform: rotate(-50deg) translateZ(0);
				transform-origin: 0 16px;
				margin: -1px 0 0 -8px; } } }

	.form-success .subs-success {
		opacity: 1;
		visibility: visible;
		transform: scale(1, 1) translateZ(0);

		span {
			&::before {
				width: 8px;
				transition: {
					delay: 0s;
					timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05); } }

			&::after {
				width: 16px;
				transition-delay: 0.12s; } } }

	.subs-error {
		position: absolute;
		top: 100%;
		left: 0;
		@include size(100%, 24px);
		@include font(14px, 24px);
		color: $form-error-color;
		@include truncate(100%);
		opacity: 0;
		z-index: -1;
		user-select: none;
		pointer-events: none;

		transition: {
			property: opacity;
			duration: 0.1s; } }

	.error ~ .subs-error {
		opacity: 1; } }

/* subscription form end */

/* user options */
$uo-item-size: $header-height $header-height; // h w
$uo-item-size--md: $header-height--md $header-height--md; // h w
$uo-item-size--sm: $header-height--sm $header-height--sm; // h w
// h w
$uo-space: 20px;
$uo-space--sm: 10px;
$uo-icon-size: 20px;
$uo-icon-size--sm: 16px;

.user-options {
	position: absolute;
	right: 0;
	top: 0;
	@include clear-spacing;
	line-height: 1; }

.user-options__item {
	position: relative;
	@include display-inline-block-top;
	min-width: nth($uo-item-size, 2);
	height: nth($uo-item-size, 1);

	@include media('<desk-ml') {
		min-width: nth($uo-item-size--md, 2);
		height: nth($uo-item-size--md, 1); }

	@include media('<desk-sm') {
		min-width: nth($uo-item-size--sm, 2);
		height: nth($uo-item-size--sm, 1); }

	& > a, & > div {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		padding: round($uo-space/2);
		color: inherit;
		line-height: 1;
		text-decoration: none;
		text-align: center;

		//      
		.touchevents & {
			user-select: none;
			-webkit-tap-highlight-color: transparent; }

		// tooltip
		&[data-title]::before {
			content: attr(data-title);
			display: block;
			min-width: 100%;
			padding: 4px 8px;
			border-radius: 2px;
			position: absolute;
			//left: 50%
			right: 0;
			top: 100%;
			@include font-light;
			@include font(1.4rem, 20px);
			text-align: center;
			letter-spacing: normal;
			z-index: 2;
			color: white;
			background-color: $base-color;
			//border: 1px solid #666
			@include antialiased;
			//===========
			opacity: 0;
			visibility: hidden;
			$prop: opacity, transform;
			$translate: 0, -5px;
			transform: translate($translate);
			transform: translate3d($translate, 0);
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed; } }

		svg {
			display: inline-block;
			vertical-align: middle;
			@include size($uo-icon-size);

			//    
			@include media('<mob-lg') {
				@include size($uo-icon-size--sm); }

			& ~ span {
				padding-left: 12px;

				//    
				@include media('<mob-lg') {
					display: none; } }

			& ~ span.hide-text {
				padding-left: 0; } }

		path {
			fill: currentColor; }

		& > span {
			@include display-inline-block-middle;
			@include font(1.6rem, 1);
			letter-spacing: normal;
			text-align: left; } }

	& > div {
		cursor: default; }

	// hover
	& > a {
		transition: {
			property: color;
			duration: $animation-speed; }

		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			border-top: 2px solid $base-color;
			will-change: opacity;
			opacity: 0;
			transition: {
				property: opacity;
				duration: $animation-speed; } }

		.no-touchevents &:hover {
			color: $base-color;

			&::after {
				opacity: 1; } } }

	& > a, & > div {
		// tooltip
		.no-touchevents &[data-title]:hover {
			&::before {
				opacity: 1;
				visibility: visible;
				$translate: 0, -50%;
				transform: translate($translate);
				transform: translate3d($translate, 0); } } }

	//&:last-child
	// > a, & > div
	//.no-touchevents &[data-title]:hover
	//	&::before
	//		$translate: 0, 0
	//		transform: translate($translate)
 }	//		transform: translate3d($translate, 0)

