// layout 
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	min-height: 100%;
	max-width: 100%;
	width: $page-layout-width;
	background-color: $bg-color-alt;
	box-shadow: 0 0 0 1px #cdcdcd; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	padding-bottom: $bottom-space--article;

	@include media('<desk-sm') {
		padding-top: $header-height--sm;
		padding-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		padding-bottom: $bottom-space--article--mob; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: relative;
	height: $header-height;
	z-index: $z-i-header;

	@include media('<desk-ml') {
		height: $header-height--md; }

	@include media('<desk-sm') {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		min-width: $page-layout-min;
		height: $header-height--sm;
		box-shadow: 0 0 20px rgba(black, 0.2); } }

.header__holder {
	position: relative;
	height: 100%;
	//padding: 0
	background-color: $header-bg;
	//box-shadow: $header-shadow
	@include clear; }

/* logo */
.logo {
	display: block;
	position: absolute;
	left: 50%;
	top: 50%;
	width: nth($logo-size, 2);
	transform: translate(-50%, -50%);

	@include media('<desk-ml') {
		width: nth($logo-size--md, 2); }

	@include media('<desk-sm') {
		width: nth($logo-size--mob, 2); }

	&::before {
		content: "";
		display: block;
		@include ratio(nth($logo-size, 2), nth($logo-size, 1));
		background-repeat: no-repeat;
		background-position: left;
		background-color: $header-bg;
		background-image: url(../../img/logo.png);
		background-size: contain; }

	a, & > span {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		@include size(100%);
		z-index: 5;

		& > em {
			@include hide-text; } } }

/* logo end */
/* === HEADER end === */

/* === NAVIGATION === */
// variables
$nav-is-open-class: 'nav-is-open';

// nav layout
.nav {
	border-top: 1px solid $border-color;
	text-align: center;
	z-index: $z-i-nav;
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: transform, opacity;
		duration: $animation-speed; }

	@include media('<desk-sm') {
		position: fixed;
		left: 0;
		top: $header-height--sm;
		width: $nav-width;
		height: calc(100% - #{$header-height--sm});
		background-color: white;
		z-index: 9999;
		$x: -100%;
		//$x: 0
		transform: translate($x, 0); // for old browser
		transform: translateX($x);

		&.#{$nav-is-open-class}, // add js
		.no-js & {
			$x: 0;
			transform: translate($x, 0); // for old browser
			transform: translateX($x); } }

	@include media('<mob-lg') {
		width: 100%; } }

.nav__layout {
	display: inline-block;
	vertical-align: top;
	text-align: left;
	position: relative;
	height: 100%;

	@include media('<desk-sm') {
		display: block;
		padding: 0;
		overflow-x: hidden;
		overflow-y: auto;

		.no-touchevents & {
			overflow-y: hidden;

			&:hover {
				overflow-y: auto; } } } }

/* nav list */
//     
.nav__list {
	position: relative;

	@include media('<desk-sm') {
		width: $nav-width;

		.touchevents & {
			width: auto; } }

	//      
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li {
		position: relative; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 8px 24px;
		color: $text-color;
		text-decoration: none;
		user-select: none;
		$prop: color, background-color, border-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<desk-ml') {
			@include padding-horizontal(nth($nav-space--sm, 2)); }

		@include media('<desk-sm') {
			@include padding-horizontal(nth($nav-space--mob, 2)); }

		//      
		.touchevents & {
			-webkit-tap-highlight-color: transparent; }

		span {
			@include font(1.6rem, round-float(24/16, 2)); } }

	&, & ul {
		list-style: none;
		padding: 0;
		margin: 0; } }

//     
.nav__drop {
	$prop: opacity, visibility, transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('>=desk-sm') {
		display: block !important; //   inline  display: none  javaScript
		position: absolute;
		left: 0;
		top: 100%;
		width: 280px;
		padding: 0;
		border: 1px solid $border-color;
		background-color: #fff;
		opacity: 0;
		pointer-events: none;
		//visibility: hidden
		$transform: -10px, 0;
		transform: translate($transform);
		transform: translate3d($transform, 0); }

	@include media('<desk-sm') {
		display: none; }

	.hover > & {
		@include media('>=desk-sm') {
			opacity: 1;
			pointer-events: auto;
			//visibility: visible
			$transform: 0, 0;
			transform: translate($transform);
			transform: translate3d($transform, 0); } }

	.no-js & {
		display: block;
		opacity: 1;
		visibility: visible;
		pointer-events: auto; } }

//    ,    
.nav__list li.has-drop > .nav__tab > a {
	@include media('<desk-sm') {
		padding-right: (round($nav-arrow-size+10)); } }

//      
.nav__list li.current {
	& > .nav__tab a,
	& > a {
		color: $base-color; } }

//       
//.nav__list li
//.is-open
//& > .nav__tab a
//	&::before
//		opacity: 1
//		transform: translate(0, 0)

//& > .nav__drop
//isplay: block

//    nav__drop style="display: block"

//       
.nav__list > li {
	float: left;
	height: $nav-height;

	@include media('<desk-sm') {
		float: none;
		height: auto; }

	& > .nav__tab {
		height: 100%;
		//padding: 10px 0

		@include media('<desk-sm') {
			padding: 0;
			height: auto; } }

	& > .nav__tab a,
	& > a {
		display: flex;
		align-items: center;
		height: 100%;
		@include padding-vertical(5px);
		@include font-alt-bold;
		color: $text-color;
		border: 0 solid transparent;
		border-bottom-width: 2px;

		@include media('<desk-sm') {
			border: none;
			@include padding-vertical(nth($nav-space--mob, 1)); }

		span {
			@include font(2.0rem, round-float(24/20, 2));

			@include media('<desk-ml') {
				font-size: 1.8rem; } } }

	// hover
	&.hover {
		//   
		@include media('>=desk-sm') {
			& > .nav__tab a,
			& > a {
				border-color: $base-color;
				color: $base-color; } } } }

//  
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//border: solid #666
	//border-width: 0 0 0 1px
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	//      
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li.has-drop > .nav__tab > & {
		@include media('<desk-sm') {
			display: block; } }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(20px);
		margin: -10px 0 0 -10px;
		background-color: $base-color;
		border-radius: 10px;
		pointer-events: none;
		transition-property: transform;
		transition-duration: $animation-speed;
		@include font(0, 0);

		&::before, &::after {
			@include pseudo();
			@include position(50%, 50%);
			height: 2px;
			width: 10px;
			margin: -1px -5px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: white;
			transition-property: background-color, transform;
			transition-duration: $animation-speed; }

		&::after {
			transform: rotate(0.25turn); } }

	//  ( )
	li.is-open > .nav__tab & i {
		//transform: rotate(0)

		&::before, &::after {
			transform: rotate(0); } } }

//      
//.nav__angle

/// 
///.no-touchevents li > .nav__tab &:hover i
//&::before, &::after
//	background-color: $base-color

//     
.nav__drop {

	//       
	a {
		color: $text-color;

		@include media('<desk-sm') {
			padding-left: #{round(nth($nav-space--mob, 2)*2)}; } }

	// hover
	li.hover {
		//   
		@include media('>=desk-sm') {
			& > .nav__tab a,
			& > a {
				background-color: $base-color;
				color: white; } } }

	.nav__drop {
		top: -1px;
		left: 100%;

		@include media('<desk-sm') {
			left: auto; }

		a {
			@include media('<desk-sm') {
				padding-left: #{round(nth($nav-space--mob, 2)*3)}; } } } }

/* nav list */
// nav opener btn
a.nav-opener {
	$btn-menu-bg: white;
	$btn-menu-color: $base-color;
	$btn-menu-size: $header-height--sm;
	$btn-menu-lines-width: 24px;
	$btn-menu-lines-height: 2px;
	$btn-menu-lines-space: 5px;
	$btn-menu-lines-color: $base-color;
	$btn-menu-animation-speed: $animation-speed;
	$btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

	$user-options-cross-width: $btn-menu-lines-width;
	$user-options-cross-height: $btn-menu-lines-height;
	$user-options-cross-color: $btn-menu-lines-color;

	// =====================

	display: none;
	position: fixed;
	left: 0;
	top: round($header-height--sm/2);
	margin-top: -#{round($btn-menu-size/2)};
	z-index: 20;
	@include size($btn-menu-size);
	text-decoration: none;
	color: $text-color;
	background-color: $btn-menu-bg;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	@include media('<desk-sm') {
		display: block; }

	.touchevents & {
		//      
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	& > span {
		@include hide-text;
		white-space: normal; }

	// 
	&::before, &::after {
		@include pseudo();
		@include size($user-options-cross-width, $user-options-cross-height);
		background-color: $user-options-cross-color;
		@include position(50%);
		margin-top: -#{round($user-options-cross-height / 2)};
		margin-left: -#{round($user-options-cross-width / 2)};
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $btn-menu-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// 
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($btn-menu-lines-width, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color;
		margin-top: -#{round($btn-menu-lines-height / 2 - 2)};
		margin-left: -#{round($btn-menu-lines-width / 2 - 2)};
		backface-visibility: hidden;
		transition: {
			property: opacity;
			duration: $btn-menu-animation-speed;
			delay: $btn-menu-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: -3px;
			@include size($btn-menu-lines-width, $btn-menu-lines-height);
			background-color: $btn-menu-lines-color;
			backface-visibility: hidden;
			transition: {
				property: transform;
				duration: $btn-menu-animation-speed;
				delay: $btn-menu-animation-delay; } }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {
		//background-color: #f3f3f3

		&::before, &::after {
			background-color: $base-color; }

		& > i {
			background-color: $base-color;

			&::before, &::after {
				background-color: $base-color; } } }

	// active
	&.nav-is-open {
		background-color: #fff;

		&::before, &::after {
			transition-delay: $btn-menu-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } } }

// overlay
//  html 
// <div class="nav-overlay nav-overlay-js">&nbsp;</div>
.overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, 0.35);
	z-index: 9998;
	opacity: 0;
	visibility: hidden;
	$prop: opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	&.nav-overlay {
		top: $header-height--sm;
		height: calc(100% - #{$header-height--sm}); }

	&.open-only-mob {
		@include media('>=desk-sm') {
			display: none; } }

	&.#{$nav-is-open-class} {
		opacity: 1;
		visibility: visible; }

	&.filters-is-open, &.request-is-open {
		opacity: 1;
		visibility: visible; } }

/* navigation end */

/* === NAVIGATION end === */

/* === FOOTER === */
// footer layout
.footer {
	position: relative;
	z-index: $z-i-footer;
	background-color: white;
	overflow: hidden;

	.font-large {
		@include font-bold;
		font-size: 2.4rem; } }

.footer-row {
	position: relative;
	display: flex;
	justify-content: space-between;
	@include padding-vertical(10rem);
	min-height: 1px;

	@include media('<desk-sm') {
		@include padding-vertical(5rem); }

	@include media('<mob-lg') {
		display: block; } }

.footer-col {
	width: calc(100% / 6);
	//     display: flex
	display: inline-block;
	//display: flex
	justify-content: flex-start;

	@include media('<mob-lg') {
		display: block;
		width: 100%;
		margin-bottom: 20px;
		padding: 0;

		&:last-child {
			margin-bottom: 0; } }

	&:nth-child(2) {
		width: calc(100% / 6 * 4); }

	&:first-child {
		padding-right: nth($grid-space, 2);

		@include media('<desk-sm') {
			padding-right: nth($grid-space--md, 2); }

		@include media('>=mob-lg') {
			justify-content: flex-start;
			text-align: left; } }

	&:last-child {
		padding-left: nth($grid-space, 2);

		@include media('<desk-sm') {
			padding-left: nth($grid-space--md, 2); }

		@include media('>=mob-lg') {
			justify-content: flex-end;
			text-align: right; } }

	p:last-child {
		margin-bottom: 0; } }

// copyright
.copyright {
	a {
		text-decoration: none;

		&:hover {
			color: $base-color; } } }

// footer logo
.footer-logo {
	display: block;
	width: 175px;
	max-width: 100%; }

// footer navigation
.footer-nav {
	overflow: hidden; }

.footer-nav__list {
	@include clear-spacing;

	&, & ul {
		list-style: none;
		margin: 0;
		padding: 0; }

	& {
		margin: -$bottom-space--article -#{nth($grid-space, 2)} $bottom-space--element 0;

		@include media('<desk-sm') {
			margin: -$bottom-space--article--md -#{nth($grid-space--md, 2)} $bottom-space--element--md 0; }

		@include media('<mob-lg') {
			margin: -$bottom-space--article--mob -#{nth($grid-space--mob, 2)} $bottom-space--element--mob 0; } }

	&:last-child {
		margin-bottom: 0; }

	a {
		padding-top: 0.5em;
		text-decoration: none;
		color: inherit;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		&:hover {
			color: $base-color; } }

	& > li {
		width: calc(100% / 4);
		display: inline-block;
		vertical-align: top;
		@include font($font-size, $line-height);
		letter-spacing: $letter-spacing;
		padding-right: nth($grid-space, 2);
		margin-top: $bottom-space--article;

		@include media('<desk-sm') {
			margin-top: $bottom-space--article--md;
			padding-right: nth($grid-space--md, 2); }

		@include media('<desk-sm') {
			margin-top: $bottom-space--article--mob;
			padding-right: nth($grid-space--mob, 2); }

		& > a {
			display: inline-block;
			vertical-align: top;
			@include font-bold;
			@include font(2.4rem, round-float(30/24, 2));

			& + ul {
				margin-top: $bottom-space--element;

				@include media('<desk-sm') {
					margin-top: $bottom-space--element--md; }

				@include media('<mob-lg') {
					margin-top: $bottom-space--element--mob; } } }

		ul {
			& > li {
				margin-top: $bottom-space--row;

				ul {
					display: none; }

				li {
					font-size: 0.85em;
					padding-left: 1em; } } } } }

/* footer navigation end */

/* developer */
.developer {
	display: block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	@include media('<mob-lg') {
		display: block; }

	i {
		@include media('<mob-lg') {
			display: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* === FOOTER end === */
